*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;;
}

body{ 
  min-height: 50vh;
  background: #f7f7f7;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.container{
  max-width: 350px;
  margin: 1rem auto;
}

h1{
  color: #333;
  font-size: clamp(1.8rem, 3vw, 2rem);
}

h2{
 font-size: 1.3rem;
 color: #333;
 padding-bottom: 2px;
 border-bottom: 1px solid #ddd;
}

.balance{
  margin: 1rem 0;
}

.balance p{
  margin-top: 3px;
  font-weight: bold;
}

.add__transaction{

}

.add__transaction h3{
  font-size: 1.2rem;
  color: #333;
  padding-bottom: 4px;
  border-bottom: 1px solid #ccc;
  margin-bottom: 1rem;
}

form{
  width: 100%;
}

input{
  padding: 8px 12px;
  display: block;
  width: 100%;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
  border-radius: 4px;
  outline: none;
}

.add__btn{
  outline: none;
  border: none;
  padding: 8px 12px;
  width: 100%;
  background: #49c549;
  color: #fff;
  font-size: 1rem;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s ease;
}

.add__btn:hover{
  background: #49c529;
}

.inc-expense{
  background: #fff;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.12),
              0 1px 3px rgba(0,0,0,0.24);
  text-align: center;  
  margin: 1rem 0;        
}

.inc-expense > div{
  flex: 1;
}

.inc-expense  > div:first-child{
 border-right: 2px solid rgb(234 234 234);
}

h4{
  font-size: 1.1rem;
  letter-spacing: 0.3px;
}

.plus{
  color: #49c529;
}

.minus{
  color: red;
}

.trans__history{
  margin: 1rem 0;
}

.trans__history ul{
  padding-top: 4px;
}

.trans__history ul li{
  padding: 10px;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.12),
              0 1px 2px rgba(0,0,0,0.24);
  list-style: none;
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  position: relative;
  font-weight: bold;
}

.plus__trasaction{
  border-right: 4px solid #49c529;
}

.minus__trasaction{
  border-right: 4px solid #ff0000;
}

.times{
  padding: 10px 6px;
  background: #ff0000;
  outline: none;
  color: #fff;
  border: none;
  cursor: pointer;
  position: absolute;
  top:0;
  left: -18px;
  height: 100%;
  display: none;
  transition: 0.2s ease;
}

.trans__history ul li:hover .times{
 display: block;
}

.sign{
  font-size: 13px;
  color: #a1a1a1;
}

@media screen and (max-width: 400px){
  .times{
    display: block !important;
  }
}

.history{
  margin: 1rem auto;
  width: fit-content;
  color: #808080;
}